
.navbar {
    position: fixed;
    background-color: var(--bg-primary);
    transition: width 200ms ease;
}

.navbar-nav {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
}

.nav-item {
    width: 100%;
}

.nav-item:last-child {
    margin-top: auto;
}

.nav-link {
    display: flex;
    align-items: center;
    height: 5rem;
    color: var(--text-primary);
    text-decoration: none;
    filter: grayscale(100%) opacity(0.7);
    transition: var(--transition-speed);
}

.nav-link:hover {
    filter: grayscale(0%) opacity(1);
    background: var(--bg-secondary);
    color: var(--text-secondary);
}

.link-text {
    display: none;
    margin-left: 1rem;
}

.nav-link svg {
    width: 3rem;
    min-width: 3rem;
    margin: 0 1rem;
}


.fa-primary {
    color: #fabcf2;
}

.fa-secondary {
    color: rgb(228, 108, 196);
}

.fa-primary,
.fa-secondary {
    transition: var(--transition-speed);
}

.logo {
    width: 100%;
}

.nav-logo {
    display: flex;
    align-items: center;
    height: 5rem;
    font-weight: bold;
    font-family: 'Major Mono Display', monospace;
    font-size: 1.2rem;
    text-transform: lowercase;
    text-decoration: none;
    background: var(--bg-secondary);
    letter-spacing: 0.4ch;

    // filter: grayscale(100%) opacity(0.7);
    // transition: var(--transition-speed);
}

.logo img{
    width: 4rem;
    min-width: 4rem;
    margin: 0.5rem;
    padding: 0;
}

.logo-text {
    display: none;
    margin-left: 1.5rem;
    color: whitesmoke;
}


.nav-logo:hover .logo-text {
    //color: rgb(226, 43, 211);
    background-image: linear-gradient(90deg, whitesmoke 25%, rgb(206, 79, 172) 50%, whitesmoke 100%);
    background-size: 250%;
    background-position: right;
    background-clip: text;
    -moz-background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    transition: background-position 1.3s ease-out;
}

/* media querys*/
@media only screen and (max-width: 600px) {
    .navbar {
        bottom: 0;
        width: 100vw;
        height: 5rem;
    }

    .nav-logo {
        flex-direction: row;
        justify-content: center;
    }

    .navbar-nav {
        flex-direction: row;
    }

    .nav-link {
        justify-content: center;
    }

    .logo img{
        width: 4rem;
        min-width: 4rem;
        margin: 0;
        padding: 0;
    }

    .nav-logo:hover {
        
        background-image: linear-gradient(38deg, var(--bg-secondary) 25%,  whitesmoke 50%, var(--bg-primary) 75%);
        background-size: 320%;
        background-position: right;
        transition: background-position 0.5s ease-out;
    }

    main {
        margin-left: 0;
        padding: 1rem;
    }
}

@media only screen and (min-width: 600px) {
    .navbar {
        top: 0;
        width: 5rem;
        height: 100vh;
    }

    .navbar:hover {
        width: 16rem;
    }

    .navbar:hover .link-text {
        display: inline;
    }

    .navbar:hover .logo-text {
        display: block;
    }

    main {
        margin-bottom: 0;
    }
}